
import React, { useEffect , useState} from 'react'
import { AppBar,  Toolbar, Box, Button, Tabs, Tab } from '@mui/material'
import { useDispatch, useSelector } from 'react-redux'
import { authActions } from '../store'
import { Link,  Outlet } from 'react-router-dom'


export default function Header() {
function showMenu(){
    document.querySelector('.css-1ik6mzw').style.display = 'block';
    console.log('HELL O',document.querySelector('.css-1ik6mzw'));
}
  const isLoggedIn = useSelector((state) => state.isLoggedIn)
  const [isAdmin, setIsAdmin] = useState(false)
  const [isSignup, setIsSignup] = useState(false)

  const dispath = useDispatch()
  const onClick = () => showMenu();
  const [value, setValue] = useState()
  useEffect(() => {
    if (localStorage.getItem('role') == 1) {
 setIsAdmin(true)
 console.log('TRUEEEEEEEEEE',isAdmin);
}
}, [])
  return (
    <div  >
      <AppBar
        position="sticky"
        sx={{
          background:
            '#f2f2f2',boxShadow: 0 
        }}
      >
        <Toolbar className='container-fluid container-xl'>
        <Link to="/">
              <img src='../assets/img/sublogs.png' width={120} /> 
        </Link>
            {!isLoggedIn ? (
              <>
          
                {' '}
                <Box display="flex" marginLeft={'auto'}>
              <Tabs color="textPrimary" value={value} onChange={(e, val) => setValue(val)}>
                {/* <NavLink to='travel'>Home</NavLink>
                <NavLink to='allusers'>AllUsers</NavLink> */}
                <Tab LinkComponent={Link} to="/" label="Home"/>
                <Tab LinkComponent={Link} to="/travel" label="Travel" />
                <Tab LinkComponent={Link} to="/food" label="Food" />
                <Tab LinkComponent={Link} to="/sports" label="Sports" />
                <Tab LinkComponent={Link} to="/about" label="About" />
                <Tab LinkComponent={Link} to="/contact" label="Contact" />
                
              </Tabs>
                <Button
                  onClick={() => setIsSignup(!isSignup)}
                  LinkComponent={Link}
                  to="/auth"
                  variant="contained"
                  sx={{ margin: 1, borderRadius: 10 }}
                  color="warning"
                >
                  Login
                </Button>
              
              
                </Box>
              
              </>
            ) :'' }
            {(!isAdmin && isLoggedIn) ? (  

              
            <Box display="flex" marginLeft={'auto'} >
             <Tabs color="textPrimary">
             <Tab LinkComponent={Link} to="/profile" label="Edit Profile" />
                <Tab LinkComponent={Link} to="/myBlogs" label="My Blogs" /> 
                <Tab LinkComponent={Link} to="/blogs/add" label="Add Blog" />
                <Tab LinkComponent={Link} to="/cat/add" label="Add Category" />
                <Tab LinkComponent={Link} to="/dashboard" label="DashBoard"/>
               
              </Tabs>
              <Button
                onClick={() => dispath(authActions.logout())}
                LinkComponent={Link}
                to="/auth"
                variant="contained"
                sx={{ margin: 1, borderRadius: 10 }}
                color="warning"
              >
                Logout
              </Button>
          </Box>
            ): (isAdmin && isLoggedIn)  ?   ( <Box display="flex" marginLeft={'auto'} >
            <Tabs color="textPrimary">
          
               <Tab LinkComponent={Link} to="/blogs" label="All Blogs" />
                <Tab LinkComponent={Link} to="/allusers" label="All Users"  />
                <Tab LinkComponent={Link} to="/addslide" label="Slide Show"  />
              
               <Tab LinkComponent={Link} to="/dashboard" label="DashBoard"/>
              
             </Tabs>
             <Button
               onClick={() => dispath(authActions.logout())}
               LinkComponent={Link}
               to="/auth"
               variant="contained"
               sx={{ margin: 1, borderRadius: 10 }}
               color="warning"
             >
               Logout
             </Button>
         </Box>):''
           
           }
        </Toolbar>
      </AppBar>
      <main>
        <Outlet />
      </main>
    </div>
  )
}




