import React, { useEffect, useState } from 'react'
import axios from 'axios'
import CatSideBar from "./CatSideBar"
// import Pagination from "https://cdn.skypack.dev/rc-pagination@3.1.15";
 

function Category({ title }) {
  const [blogs, setBlogs] = useState()
  const sendRequest = async () => {
    const res = await axios.get('http://192.168.1.131:5000/api/news')
      .catch((err) => console.log(err))
    //res.header("Access-Control-Allow-Origin", "true");
    const data = await res.data
    return data
  }
  useEffect(() => {
    sendRequest().then((data) => setBlogs(data.data.filter(function (blog) { return blog.category_id === title }).filter(function (blog) { return blog.status === 0 })))

  }, [])

  const [perPage, setPerPage] = useState(10);
  const [size, setSize] = useState(perPage);
  const [current, setCurrent] = useState(1);
  const PerPageChange = (value) => {
    setSize(size);
    const newPerPage = Math.ceil(blogs.length / size);
    if (current > newPerPage) {
      setCurrent(newPerPage);
    }
  }
  const getData = (current, pageSize) => {
    // Normally you should get the data from the server
    return blogs.slice((current - 1) * pageSize, current * pageSize);
  };
  //.filter(function (blog) {return blog.category_id === title}).filter(function (blog) {return blog.status === 0})
  const PaginationChange = (page, pageSize) => {
    setCurrent(page);
    setSize(pageSize)
  }
  //console.log(getData);
  const PrevNextArrow = (current, type, originalElement) => {
    if (type === 'prev') {
      return <button><i className="fa fa-angle-double-left"></i></button>;
    }
    if (type === 'next') {
      return <button><i className="fa fa-angle-double-right"></i></button>;
    }
    return originalElement;
  }

  return (
    <section>
      <div className="container">
        <div className="row">

          <div className="col-md-9 aos-init " data-aos="fade-up">
            <h3 className="category-title">Category: {title == 1 ? title = 1 : title == 2 ? title = 2 : title = 3}</h3>
            {/* <Pagination
              className="pagination-data"
              showTotal={(total, range) => `Showing ${range[0]}-${range[1]} of ${total}`}
              onChange={PaginationChange}
              total={getData.length}
              current={current}
              pageSize={size}
              showSizeChanger={false}
              itemRender={PrevNextArrow}
              onShowSizeChange={PerPageChange}
            /> */}
            <br />
            {blogs && getData(current, size).map((data) => {
              return (
                <div className="d-md-flex post-entry-2 half">
                  <a href="post" className="me-4 thumbnail">
                    <img src={data.hero_img} alt="" className="img-fluid" />
                  </a>
                  <div>
                    <div className="post-meta"><span className="date">{data.category_id == 1 ? 'TRAVEL' : data.category_id == 2 ? 'FOOD' : 'SPORTS'}</span> <span className="mx-1"></span> <span>Jul 5th '22</span></div>
                    <h3><a href="post">{data.title}</a></h3>
                    <p dangerouslySetInnerHTML={{ __html: data.summary }}></p>
                    <div className="d-flex align-items-center author">
                      <div className="photo"><img src="assets/img/person-2.jpg" alt="" className="img-fluid" /></div>
                      <div className="name">
                        <h3 className="m-0 p-0">{data.id}</h3>
                      </div>
                    </div>
                  </div>
                </div>
              )
            })
            }
            <div className="text-start py-4">
              {/* <Pagination
                className="pagination-data"
                showTotal={(total, range) => `Showing ${range[0]}-${range[1]} of ${total}`}
                onChange={PaginationChange}
                total={getData.length}
                current={current}
                pageSize={size}
                showSizeChanger={false}
                itemRender={PrevNextArrow}
                onShowSizeChange={PerPageChange}
              /> */}
            </div>
          </div>

          <CatSideBar />
        </div>
      </div>
    </section>
  )
}

export default Category