import React, { useEffect, useState }  from 'react'
import axios from 'axios'




export default function AllUsers() {
const [users, setUsers] = useState()

const sendRequest = async () => {
    const res = await axios
      .get(`http://192.168.1.131:5000/api/users/`) 
      .catch((err) => console.log(err))
    const data = await res.data
    return data
  }
  var id;

 
  var status;
   useEffect(() => {
     sendRequest().then((data) => setUsers(data.data))
   }, []);
   const sendRequestStatus = async () => {
    const res = await axios
      .put(`http://192.168.1.131:5000/api/users/updatestatus/${id}`, {
        status: status
      })
      .catch((err) => console.log(err))

    const data = await res.data

    return data
  }

  const handleSubmit = (e) => {
   // e.preventDefault()
    console.log(e.status);
     id = e.id;
     if(e.status == 1){
      status = 0
     }else{
      status = 1
     }
     console.log(status);
    sendRequestStatus()
      .then((data) => window.location.reload())
      
  }
console.log('----------------',users);

  return (
    <div  className='container-xl ' >
       
     <h1>All Users</h1>
     {users &&
          users.map((user, index) => (
            <div data-key={index} item xs="4" spacing={2}>
             <p>
                {user.name}
             </p>
           
             <button className='btn' id={`de_${user.status}`} onClick={() => handleSubmit(user)}>deactivate</button>
             <button className='btn' id={`a_${user.status}`} onClick={() => handleSubmit(user)}>activate</button>
             </div>
      
          ))}
    </div>
  )
}


